@font-face {
  font-family: xoro;
  src: url(./fonts/A\ new\ Heard\ Decorative.otf);
  font-weight: bolder;
}

.game-board {
  display: grid;
  margin-top: 70px;

  grid-template-rows: 175px 175px 175px;
  grid-template-columns: 175px 175px 175px;
}




