  background-size: cover;
  
}
.menu{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  min-height: 100vh;
  
}
.text{
  font-family: xoro;
  transition: transform .4s ease;
}
.text:hover{
  transform: scale(1.34);
}
.select{
  display: flex;
  flex-direction: column;
  justify-content:center ;
  align-items: center;
  text-align: center;
  font-size: 50px;
  color: white;

}

.select span{
  margin: 0px 100px;
  font-size: 70px;
  font-family: 'xoro';
  transition: transform .2s ease;
}

.select-items{
  display: flex;
  justify-content: space-around;
  cursor: pointer;
}
.select-items span:hover{
  transform: scale(1.5);
}
.menu-i{

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;

  margin-top:-87.5vh;
  position: relative;
 
}
.menu-item {
   font-size: 100px;
  color: white;
  cursor: pointer;
  margin: 100px;
 
 
  
  
}


.game-options{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  
  
  font-size: 50px;
  color: white;
}
.game-options span{
  padding-right: 70px ;
  font-family: 'xoro';
  font-size: 80px;
  
}
.game-options-item{
  font-size: 50px;
  color: white;
  cursor: pointer;

}
.menu-inner{
  color:white
}
.menu-item:hover,.game-options-item:hover{
  text-shadow: 2px 4px 0px rgba(20, 20, 200, 0.5);
}



.box {
  background: #444;
  border: 2px solid #555;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #aaa;
  cursor: grab;
  font-size: 175px;
  position: relative;
  background-size: cover;
  background: url('./img/chalkboard.jpg') center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  margin: 3px;
 transition: transform .2s ease;
 
}
.box:hover{
  z-index:10;
  transform: scale(1.23);
  background-size: cover;
  background: url('./img/chalk-lighter.jpg') center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  margin: 3px;
  
  
 
}

.finish{
  opacity: 0.3;
}
.hide {
  display: none;
}
.win {
  transform: scale(1.15);
  z-index:9;
  background-size: cover;
  background: url('./img/chalk-lighter.jpg') center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  margin: 3px;

}
div.box[data-pro="X"] {
  color:white;
  font-size: 150px;
  font-family:'xoro';
}

div.box[data-pro="O"] {
  color: white;
  font-size: 150px;
  font-family: 'xoro';
  
}
@media only screen and (max-width: 600px) {
  .game-board{
    grid-template-rows: 100px 100px 100px;
    grid-template-columns: 100px 100px 100px;


  }
  .menu-i{
    margin-top: -72vh;
  }
  div.box[data-pro="X"] {
    color:white;
    font-size: 70px;
    font-family:'xoro';
  }
  
  div.box[data-pro="O"] {
    color: darkgray;
    font-size: 70px;
    font-family: 'xoro';
    
  }
  .menu-item{
    font-size: 50px;
  }
  .select{
    font-size: 20px;
  }
  .select span{
    font-size: 50px; 
    
  }
  .menu-item{
    font-size: 50px;
  }
  .game-options span{
    font-size: 40px;
  }
  .game-options-item{
    font-size: 20px;
  }
}

